.no-highlight() {
  border-color: #e5e5e5;
  box-shadow: none;
}

.search {
  display: flex;
  width: 100%;
  position: relative;

  :global {
    .anticon-cross {
      position: absolute;
      width: 18px;
      height: 18px;
      right: 84px;
      cursor: pointer;
      color: #fff;
      line-height: 18px;
      border-radius: 50%!important;
      background-color: rgba(0, 0, 0, .16);
      top: 7px;
      // opacity:
    }

    .ant-select {
      width: 80px;
      flex-shrink: 0;
      flex-grow: 0;

      &.ant-select-focused .ant-select-selection,
      &.ant-select-open .ant-select-selection,
      .ant-select-selection:active,
      .ant-select-selection:focus,
      .ant-select-selection:hover {
        .no-highlight();
      }
    }

    .ant-input {
      &:focus,
      &:hover {
        .no-highlight();
      }
      flex-shrink: 1;
      flex-grow: 1;
    }

    .ant-btn {
      width: 80px;
      flex-shrink: 0;
      flex-grow: 0;
    }
  }
}

@border-radius-base: 3px;@border-radius-sm: 2px;@shadow-color: rgba(0,0,0,0.05);@shadow-1-down: 4px 4px 40px @shadow-color;@border-color-split: #f4f4f4;@border-color-base: #e5e5e5;@menu-dark-bg: #3e3e3e;@text-color: #666;@font-family: monospace,-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", SimSun, sans-serif;


