
body{
  overflow-y: auto!important;
}
.normal {
  max-width: 1000px;
  margin: 5px auto;
  padding: 1em;
  background-color: white;
}

.normalRow{
  max-width: 1000px;
  margin: 5px auto;
  background: white;
  padding: 1em;
}
.normalRow h2{
  text-align: center;
  color: red;
}
.lawyerRight li{
  font-size: 14px;
  margin: 5px;
}
.lawyerImg{
  margin-right: 20px;
  padding: 5px;
  float: left;
}
.lawyerProfile{
  margin-left: 5px;
  margin-top: 50px;
  width: 100%;
}
.lawyerProfile h3{
  width: 100%;
  background-color: #e5e5e5;
  height: 30px;
  line-height: 30px;
}
.lawyerProfile h3 span{
  margin-left: 10px;
}
.lawyerProfile p{
  width: 100%;
  border: 1px solid #e5e5e5;
  padding: 5px;
}
.lawyerProfile p span{
  margin: 5px;
}
.user_profile_table{
  width: 600px;
  border-collapse:collapse;
}
.user_profile_table tr td{
  text-align: left;
  padding:15px 15px 15px 0px;
}
.user_profile_table tr td a{
  display: block;
  width: 78px;
  height: 22px;
  text-align: center;
  line-height: 22px;
  border: 1px solid #0496ff;
  font-size: 14px;
  font-family: '微软雅黑';
  border-radius: 5px;
}
.band_emails{
  background-color: #f5f5f5;
}
.count_edit{
  color: #0496ff;
}
.count_rightband{
  color:#ffffff;
  background-color:#0496ff ;
}
.signpassword{
  border-bottom: 1px solid #e5e5e5;
}

@border-radius-base: 3px;@border-radius-sm: 2px;@shadow-color: rgba(0,0,0,0.05);@shadow-1-down: 4px 4px 40px @shadow-color;@border-color-split: #f4f4f4;@border-color-base: #e5e5e5;@menu-dark-bg: #3e3e3e;@text-color: #666;@font-family: monospace,-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", SimSun, sans-serif;


