@import "../vars.less";
:global {

  .loginModal .ant-modal{
    width: 320px!important;
    height:320px!important;
  }
  .loginModal .shadow{
    box-shadow: none;
  }
  .topmenubar{
    height: 60px;
    background: rgba(0,0,0,0.2);
    position: absolute;
    width: 100%;
    z-index:2;
  }
  .topmenubar.minibar{
    background: #FFFFFF;
  }
  .contentcol{
    width: 1000px;
    margin: 0 auto;
    background: none;
    position:relative;
  }
  .topmenu{
    border:none;
    background: none;
    height:60px;
  }
  .topmenu li{
    line-height: 58px;
  }
  .topmenu li.ant-menu-submenu,
  .topmenu li.ant-menu-item a,
  .topmenu li.ant-menu-item a:link,
  .topmenu li.ant-menu-item a:hover,
  .topmenu li.ant-menu-item a:active,
  .topmenu li.ant-menu-item a:visited
  {
    color: white;
    font-size: 16px;
    text-decoration:none;
  }
  .topmenubar.minibar .topmenu li.ant-menu-submenu,
  .topmenubar.minibar .topmenu li.ant-menu-item a,
  .topmenubar.minibar .topmenu li.ant-menu-item a:link,
  .topmenubar.minibar .topmenu li.ant-menu-item a:hover,
  .topmenubar.minibar .topmenu li.ant-menu-item a:active,
  .topmenubar.minibar .topmenu li.ant-menu-item a:visited
  {
    color: rgba(0,0,0,0.7);
  }
  .topmenu li.ant-menu-submenu li.ant-menu-item-active,
  .topmenu li.ant-menu-submenu li.ant-menu-item:hover{
    background-color: #ecf6fd;
  }
  .topmenu li.ant-menu-submenu li.ant-menu-item a{
    color: rgba(0,0,0,0.7);
    text-decoration: none;
  }


  //.ant-menu-horizontal.topmenu>.ant-menu-item:hover,
  //.ant-menu-horizontal.topmenu>.ant-menu-item-active{
  //  color: yellow;
  //  border-bottom: 2px solid yellow;
  //}
  //.ant-menu-horizontal.topmenu>.ant-menu-item>a:hover,
  //.ant-menu-horizontal.topmenu>.ant-menu-item-active>a:hover{
  //  color: yellow;
  //}

  .topslider{
    height: 640px;
  }

  .topslider>.slick-dots{
  }

  .topbanner{
    height: 640px;
    -webkit-background-size:100% 100%;
    background-size:100% 100%;
    background: #FFFFFF none no-repeat center center;
  }
  .topbannerTitle{
    width: 1000px;
    height: 100%;
    margin: auto;
    text-align: center;
  }
  .headertitle{
    padding-top: 200px;
    font-family: '微软雅黑';
    font-size: 40px;
    color: white;
    font-weight: bold;
    margin-bottom: 20px;
  }
  .headercontent{
    font-family: '微软雅黑';
    font-weight: bold;
    font-size: 30px;
    color: white;
  }
  .secondbanner{
    height: 508px;
    -webkit-background-size:100% 100%;
    background-size:100% 100%;
    background: #FFFFFF none no-repeat  center center;
    margin-bottom: -80px;
  }
  .thirdbanner{
    height: 430px;
    -webkit-background-size:100% 100%;
    background-size:100% 100%;
    background: #FFFFFF none no-repeat  center center;
    margin-bottom: -80px;
  }
  .funcs{
    display: block;
    width: 170px;
    height: 234px;
    margin: 66px auto;
    background-size:100% 100%;
  }

  .funcs.profile{
    background-image: url("http://oonavif60.bkt.clouddn.com/default/index/funcs/ButtonProfile.png");
  }
  .funcs.profile:hover{
    background-image: url("http://oonavif60.bkt.clouddn.com/default/index/funcs/ButtonProfile2.png");
  }
  .funcs.serviceitems{
    background-image: url("http://oonavif60.bkt.clouddn.com/default/index/funcs/ButtonService.png");
  }
  .funcs.serviceitems:hover{
    background-image: url("http://oonavif60.bkt.clouddn.com/default/index/funcs/ButtonService2.png");
  }
  .funcs.cases{
    background-image: url("http://oonavif60.bkt.clouddn.com/default/index/funcs/ButtonCases.png");
  }
  .funcs.cases:hover{
    background-image: url("http://oonavif60.bkt.clouddn.com/default/index/funcs/ButtonCases2.png");
  }
  .funcs.articles{
    background-image: url("http://oonavif60.bkt.clouddn.com/default/index/funcs/ButtonArticles.png");
  }
  .funcs.articles:hover{
    background-image: url("http://oonavif60.bkt.clouddn.com/default/index/funcs/ButtonArticles2.png");
  }
  .onlinewnd{
    position:absolute;
    top: 50%;
    right: 10px;
    z-index: 100;
    margin-top: -100px;
  }
  .lawyerimg{
    width: 140px;
    height: 140px;
  }
  .righttopicons{
    float:right;
    font-size: 2em;
  }
  .righttopicons li{
    float:left;
    padding: 0 1em;
  }
  .righttopicons li:hover{
    color: #108ee9;
  }
  .online{
    color: #00FF00;
  }
  .offline{
    color: #999999;
  }
}


@border-radius-base: 3px;@border-radius-sm: 2px;@shadow-color: rgba(0,0,0,0.05);@shadow-1-down: 4px 4px 40px @shadow-color;@border-color-split: #f4f4f4;@border-color-base: #e5e5e5;@menu-dark-bg: #3e3e3e;@text-color: #666;@font-family: monospace,-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", SimSun, sans-serif;


