
body{
  overflow-y: auto!important;
}
.normal {
  max-width: 1000px;
  margin: 5px auto;
  padding: 1em;
  background-color: white;
}
.user_detail{
  max-width: 1000px;
  margin: 5px auto;
  padding: 11px 34px;
  background-color: white;
  position: relative;
  font-family: '微软雅黑';
  font-size: 14px;
  color:#666666;
  min-height: 700px;
}
.normalRow{
  max-width: 1000px;
  margin: 5px auto;
  background: white;
  padding: 1em;
}
.normalRow h2{
  text-align: center;
  color: red;
}
.lawyerRight li{
  font-size: 14px;
  margin: 5px;
}
.lawyerImg{
  margin-right: 20px;
  padding: 5px;
  float: left;
}
.lawyerProfile{
  margin-left: 5px;
  margin-top: 20px;
  width: 100%;
}
.lawyerProfile h3{
  width: 100%;
  background-color: #e5e5e5;
  height: 30px;
  line-height: 30px;
}
.lawyerProfile h3 span{
  margin-left: 10px;
}
//.lawyerProfilediv{
//  width: 100%;
//  border: 1px solid #e5e5e5;
//  padding: 5px;
//}
.lawyerProfilediv span{
  margin: 5px;
}
.use_picborder{
  padding: 5px;
  width: 145px;
  height: 145px;
  border-radius: 5px;
  position: relative;
  top: -55px;
  z-index: 22;
  float: left;
  background-color: #ffffff;
}
.user_detail_picture{
  width: 135px;
  height: 135px;
}
.user_profile_top{
  width: 767px;
  float: left;
  margin-left: 15px;
}
.user_paybut{
  width: 78px;
  height: 22px;
  background-color: #0496ff;
  color: #ffffff;
  font-family: '微软雅黑';
  font-size: 12px;
  display:inline-block;
  text-align: center;
  border-radius: 5px;
  padding-top: 2px;
  margin-top: 5px;
}
.user_myaccount{
  width: 240px;
  height: auto;
  text-align: center;
  border: 1px solid #f5f5f5;
  float: left;
}
.user_myaccount_theme{
  width: 100%;
  height: 34px;
  background-color: #f5f5f5;
  text-align: center;
  line-height: 34px;
  font-size: 12px;
  padding-right: 72px;
}
.clearfix{
  clear: both;
}
.user_myaccount p{
  padding: 25px;
}
.mycountright{
  width: 645px;
  padding-top: 5px;
  float: left;
  margin-left: 46px;
}
.user_myaccount p:hover{
  border-left:3px solid #0496ff;
  color:#0496ff;
  font-size: 16px;
}
.user_count_theme{
  padding-bottom: 10px;
  border-bottom: 3px solid #0496ff;
  color: #0496ff;
  margin-bottom: 16px;
  width: 600px;
  height: 34px;
}
.leftborder_show{
  border-left:3px solid #0496ff;
  color:#0496ff;
  font-size: 16px;
}
.user_profile_name{
  font-size: 26px;
  font-family: '微软雅黑';
  font-weight: bold;
  color: #333333;
}
.user_profile_top .saying{
  font-size: 14px;
  font-family: '微软雅黑';
  color: #666666;
}
.user_myaccount_theme img{
  margin-right: 8px;
}
.changesuccess{
  text-align: center;
  height: 200px;
  padding-top: 100px;
}
.fault_tip{
  width: 100%;
  text-align: center;
  color: red;
  font-size: 20px;
}
.user_profile_top .remaincount{
  font-family: "Microsoft Yahei";
  font-size: 14px;
  color: #ff7e00;
  padding-right: 20px;
}
.user_count_theme a{
  color: grey;
  font-size: 14px;
  padding: 10px 10px 0 0;
}
.user_count_theme a:hover{
  color:#0496ff;
  font-size: 16px;
}
.user_count_theme .runing{
  color:#0496ff;
}

@border-radius-base: 3px;@border-radius-sm: 2px;@shadow-color: rgba(0,0,0,0.05);@shadow-1-down: 4px 4px 40px @shadow-color;@border-color-split: #f4f4f4;@border-color-base: #e5e5e5;@menu-dark-bg: #3e3e3e;@text-color: #666;@font-family: monospace,-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", SimSun, sans-serif;


