
.normal {
}

:global{

  .avatar{
    width: 50px;
    height: 50px;
    border-radius: 25px;
    position: absolute;
    left: 50%;
    margin: 10px 10px 10px -25px;
  }
  .avatardiv{
    margin: 10px auto;
    width: 50px;
    height: 50px;
    background: #00a2d4;
    border-radius: 25px;
    text-align: center;
  }
  .avatarName{
    font-size: 30px;
    line-height: 50px;
    color: white;
  }

  .myMsg{
    margin: 5px 1rem 5px 1em;
    padding: 5px 1em;
    height: auto;
    width: auto;
    min-width: 5em;
    max-width: 95%;
    float:right;
    position: relative;
    background: lawngreen;
    -moz-border-radius: 0.5em;
    -webkit-border-radius: 0.5em;
    border-radius: 0.5em;
    overflow:visible;
    word-wrap: break-word;
  }
  .myMsg:before{
    content:"";
    position: absolute;
    left:100%;
    top: 0.5em;
    width:0;
    height:0;
    border-top: 0.3em dotted transparent;
    border-left: 0.6em dotted lawngreen;
    border-bottom: 0.3em dotted transparent;
  }

  .remoteMsg{
    margin: 5px 1rem 5px 1em;
    padding: 5px 1em;
    height: auto;
    width: auto;
    min-width: 5em;
    max-width: 95%;
    position: relative;
    background: lightskyblue;
    -moz-border-radius: 0.5em;
    -webkit-border-radius: 0.5em;
    border-radius: 0.5em;
    overflow:visible;
    float:left;
    word-wrap: break-word;
  }

  .remoteMsg:before{
    content:"";
    position: absolute;
    right:100%;
    top: 0.5em;
    width:0;
    height:0;
    border-top: 0.3em dotted transparent;
    border-right: 0.6em dotted lightskyblue;
    border-bottom: 0.3em dotted transparent;
  }

}

@border-radius-base: 3px;@border-radius-sm: 2px;@shadow-color: rgba(0,0,0,0.05);@shadow-1-down: 4px 4px 40px @shadow-color;@border-color-split: #f4f4f4;@border-color-base: #e5e5e5;@menu-dark-bg: #3e3e3e;@text-color: #666;@font-family: monospace,-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", SimSun, sans-serif;


