
.normal {
}

:global{

  .render-page>div{
    margin: 0px auto;
  }

  .render-page.rotate0{
    transform: rotateZ(0deg);
  }

  .render-page.rotate90{
    transform: rotateZ(90deg);
  }

  .render-page.rotate180{
    transform: rotateZ(180deg);
  }

  .render-page.rotate270{
    transform: rotateZ(270deg);
  }

  @font-face {
    font-family: 'fpanticon';  /* project id 258626 */
    src: url('//at.alicdn.com/t/font_9d7r1p9t9hz4obt9.eot');
    src: url('//at.alicdn.com/t/font_9d7r1p9t9hz4obt9.eot?#iefix') format('embedded-opentype'),
    url('//at.alicdn.com/t/font_9d7r1p9t9hz4obt9.woff') format('woff'),
    url('//at.alicdn.com/t/font_9d7r1p9t9hz4obt9.ttf') format('truetype'),
    url('//at.alicdn.com/t/font_9d7r1p9t9hz4obt9.svg#fpant') format('svg');
  }

  .fpanticon{
    display: inline-block;
    font-style: normal;
    font-size: 1.5em;
    vertical-align: baseline;
    text-align: center;
    text-transform: none;
    line-height: 2;
    text-rendering: optimizeLegibility;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  .fpanticon:before {
    display: block;
    font-family: "fpanticon" !important;
