.list {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  margin-right: -24px;

  li {
    width: 160px;
    text-align: center;
    cursor: pointer;
    height: 100px;
    transition: all 0.5s ease;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-right: 24px;
    margin-bottom: 24px;
    box-sizing: border-box;
    border: solid 1px transparent;

    &:hover{
      border: solid 1px #f5f5f5;
    }
  }

  .name {
    margin-top: 16px;
  }

  :global .ico {
    font-size: 48px;
  }
}

@border-radius-base: 3px;@border-radius-sm: 2px;@shadow-color: rgba(0,0,0,0.05);@shadow-1-down: 4px 4px 40px @shadow-color;@border-color-split: #f4f4f4;@border-color-base: #e5e5e5;@menu-dark-bg: #3e3e3e;@text-color: #666;@font-family: monospace,-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", SimSun, sans-serif;


